import { Box, Image, Title, Subtitle } from 'bloomer';

export default function Details({ name, area, color, photo }) {
    const style = {
        marginLeft: 'auto', 
        marginRight: 'auto',
        padding: 10,
        border: `0.2rem solid ${color}`, 
        borderRadius: '50%'
    }
    return (
        <Box hasTextAlign='centered'>
            <Image style={style} isSize='128x128' src={photo} />
            <Title>{name}</Title>
            <Subtitle isSize={5}>{area}</Subtitle>
        </Box>
    );
}